.edit_profile_content_wrapper {
	overflow-y: auto;
	height: 100%;
}

.editprofile_content{
	margin: auto;
}
.createprofile_heading{
text-align: center;
height: 50px;
font-size: 27px;
color: #083952 !important;
margin-bottom: 15px;
