@media (max-width: 575px) {
.createprofile_back_icon,
.createprofile_go_back {
		position: static;
		transform: initial;
		display: inline-block;
		vertical-align: middle;
}
.createprofile_back_icon_text {
		text-align: left;
		margin-top: 10px;
}
.createprofile_heading {
		line-height: 1;
		height: auto;
}
}

.update_profile .btn{
	color: #fff;
	background-color: #5fcc40 !important;
	border-color: #5fcc40 !important;
	width: 100%;
	margin-top: 10px;
	font-size: 18px;
}
.btn-primary:focus{
	box-shadow: none !important;
	outline: none;
}
.user_details .form-control:focus{
	box-shadow: none !important;
}
.edit_profile_form_fields_wrapper {
	background: #fff;
	padding: 15px;
	border-radius: 4px;
	overflow: auto;
	height: calc(100vh - 170px);
}
.edit_profile_form_fields_wrapper .form-group {
	margin-bottom: 1rem !important;
}
.edit_profile_form_fields_wrapper .form-group,
.edit_profile_form_fields_wrapper button {
	margin-top: 1rem;
}
.edit_profile_form_fields_wrapper .form-control {
	border: 1px solid lightgray;
}


.email-checkbox {
	position: absolute;
	opacity: 0;
}
.email-checkbox + label {
	position: relative;
	cursor: pointer;
	padding: 0;
	margin-bottom: 0px;
}
.email-checkbox + label:before {
	content: '';
	/* margin-right: 10px; */
	display: inline-block;
	vertical-align: text-top;
	width: 20px;
	height: 20px;
	border: 1px solid #a6a6a6;
	background-color: #ffffff;
	border-radius: 4px;
	margin-right: 20px;
}
.email-checkbox:hover + label:before {
	/* background: #8DC73F; */
	border: 1px solid #000;
}
.email-checkbox:checked + label:before {
	background: #8DC73F;
}
.email-checkbox:disabled + label {
	color: #b8b8b8;
	cursor: auto;
}
.email-checkbox:disabled + label:before {
	box-shadow: none;
	background: #ddd;
}
.email-checkbox:checked + label:after {
	content: '';
	position: absolute;
	left: 5px;
	top: 11px;
	background: white;
	width: 2px;
	height: 2px;
	box-shadow: 2px 0 0 white, 4px 0 0 white, 4px -2px 0 white, 4px -4px 0 white, 4px -6px 0 white, 4px -8px 0 white;
	-webkit-transform: rotate(45deg);
					transform: rotate(45deg);
}
.error-message{
		color: red;
}
.edit_profile_form_fields_wrapper .editor_cls button {
	margin-top: 0;
}
.editprofile_content.last_row {
	margin-bottom: 20px;
}
.form-drop-control {
	width: 100%;
	display: block;
}
